import React, { useState } from 'react';
import { BrowserRouter, Link } from 'react-router-dom';
import {
  DesktopOutlined,
  FileOutlined,
  PieChartOutlined,
  TeamOutlined,
  UserOutlined,
} from '@ant-design/icons';
import type { MenuProps } from 'antd';
import { Breadcrumb, Layout, Menu, theme } from 'antd';
import LayoutSlider from './layouts/LayoutSider';

const { Header, Content, Footer, Sider } = Layout;

type MenuItem = Required<MenuProps>['items'][number];

function getItem(
  label: React.ReactNode,
  key: React.Key,
  icon?: React.ReactNode,
  children?: MenuItem[],
): MenuItem {
  return {
    key,
    icon,
    children,
    label,
  } as MenuItem;
}

const items: MenuItem[] = [
  getItem('Option 1', '1', <PieChartOutlined />),
  getItem('About Me', 'sub1', <UserOutlined />, [
    getItem('Education', '3'),
    getItem('Employment', '4'),
    getItem('Experience', '5'),
  ]),
  getItem('Transformation', 'sub2', <TeamOutlined />, [
    getItem('Business', '6'), 
    getItem('People', '7'),
    getItem('Technology', '8')
  ]),
  getItem('Skills', '9', <FileOutlined />),
  getItem('Articles', '2', <DesktopOutlined />),
];

const App: React.FC = () => {
  return (
    <div>
       <BrowserRouter>
          <LayoutSlider/>
       </BrowserRouter>
    </div>
 );
};


